.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.body {
  margin-left: 5%;
  margin-right: 5%;
  margin-bottom: 5%;
  margin-top: 1%;
}

.line {
  height: 3px;
  border: none;
  color: rgb(70, 72, 76);
  background-color: rgb(70, 72, 76);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
